.page-main {
  background-color: @white;
}

.page-main__heading {
  .offset();
  padding: 32px 20px 19px;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-align: center;

  @media (min-width: @tablet-width) {
    padding: 63px 20px 24px;
    font-size: 30px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 99px;
  }
}

.page-main__slogan {
  .offset();
  padding: 35px 25px 18px;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-align: center;

  @media (min-width: @tablet-width) {
    padding: 43px 75px 19px;
    font-size: 21px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 54px;
    margin: 0 auto;
    max-width: 610px;
  }
}

.page-main__description {
  .offset();
  padding: 10px 20px 10px;
  margin-bottom: 20px;
  font-size: 14px;
  line-height: 24px;
  color: @dark-grey;
  text-align: center;

  @media (min-width: @tablet-width) {
    max-width: 440px;
    margin: 0 auto;
  }
}

.page-main__map {
  height: 350px;
  background-color: @light-grey;
}

.page-main__map-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
